@import "../../node_modules/sass-mediaqueries/media-queries";
@import "../styles/constants.scss";
footer.primaryFooter {
  background-color: #598445;
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  > .leftSideText {
    display: inline;
  }
  padding: 1em;
  font-weight: bolder;
}

// feature phones
@include screen(320px, 640px) {
  footer.primaryFooter {
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
  }
}

// ipads and other tablets
@include screen(641px, 1366px) {
  footer.primaryFooter {
    flex-wrap: wrap;
    height: 100px;
    justify-content: center;
  }
}

a.footerLink,
div.rightSideText a {
  color: black;
  &:hover {
    color: white;
    text-decoration: underline;
  }
}
