@import "./node_modules/sass-mediaqueries/media-queries";
@import "./constants.scss";

body {
  font-family: "Roboto", sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.contentWrapper {
  flex: 1 0 auto;
}

footer {
  flex-shrink: 0;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

button {
  outline: none !important;
}
