@import "../../node_modules/sass-mediaqueries/media-queries";
@import "../styles/constants.scss";
.landingWrapper {
  height: 770px;
  display: flex;
  justify-content: center;
  align-items: center;
  .boxWrapper {
    // margin-top: 116px;
    // background: rgba($color: #fff, $alpha: 0.5);
    // opacity: 0.5;
    box-sizing: border-box;
    border-radius: 10px;
    width: 95%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    aside {
      border: 1px solid #598445;
      background-color: rgba($color: #f0f7eb, $alpha: 0.8);
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 3rem;
      border-radius: 10px;
      h2 {
        margin-top: 41px;
        font-size: 34px;
        line-height: 40px;
        color: #000;
        margin-bottom: 14px;
      }
      p {
        font-weight: 300;
        font-size: 18px;
        line-height: 1.6rem;
        &:last-of-type {
          font-weight: bold;
          margin-bottom: 1.5rem;
        }
      }
    }
    /*
    Deprecated, using LightButton now
    */
    // button {
    //   padding: 9px 16px 9px 16px;
    //   background-color: $secondaryProgressBtnColor;
    //   border-radius: $progressBtnRadius;
    //   border: 1px solid $secondaryProgressBtnBorderColor;
    //   transition: all 0.2s linear;
    //   width: 100px;
    //   height: 40px;
    //   font-size: 15px;
    //   &:hover {
    //     background-color: $primaryProgressBtnColor;
    //     border: 1px solid $primaryProgressBtnBorderColor;
    //     color: white;
    //   }
    // }
    // margin: 0 auto;
  }
}
@include screen(320px, 640px) {
  .landingWrapper {
    height: auto;
    display: block !important;
    .boxWrapper {
      // margin-top: 116px;
      background: rgba($color: #fff, $alpha: 0.5);
      // opacity: 0.5;
      border: 1px solid #598445;
      box-sizing: border-box;
      border-radius: 0px;
      width: 100%;
      height: auto;
      padding: 1em;
      aside {
        border: 0px;
        // background-color: rgba($color: #f0f7eb, $alpha: 0.5);
        background-color: transparent;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 1rem;
        border-radius: 10px;
        h2 {
          margin-top: 41px;
          font-size: 34px;
          line-height: 40px;
          color: #000;
          margin-bottom: 14px;
        }
        p {
          font-weight: 300;
          font-size: 18px;
          line-height: 1.6rem;
          &:last-of-type {
            font-weight: bold;
          }
        }
      }
      // margin: 0 auto;
    }
  }
}
@include screen(641px, 950px) {
  aside.landingWrapper {
    .boxWrapper {
      width: 100% !important;
      border-radius: 0px;
      > aside {
        border: 0px;
        padding: 1em;
        > h2 {
          margin-top: 0px;
        }
      }
    }
  }
}
